export default {
	data() {
		return {
			windowHeight:'100%',
			sjvalue:11,
			mimavalue:'',
			dataMima:''
		};
	},
	onLoad() {
		var _me = this;
		
		uni.getSystemInfo({
			success: function(res) {
				_me.windowHeight = res.windowHeight + 'px';
			}
		});
	},
	methods:{
		onKeyInput:function(e){
			this.sjvalue = e.target.value;
		},
		mimaInput:function(e){
			this.mimavalue = e.target.value;
		},
		btnDenglu:function(){
			var me = this;
			uni.getStorage({
				key: 'howeKey',
				success: function (res) {
					var sjvalue = res.data.shouji;
					var mima = res.data.mima;
					
					if(me.sjvalue.length !== 11 || me.sjvalue !== sjvalue){
						uni.showModal({
							title: '提示',
							content: '请输入正确手机号码！',
							showCancel: false
						});
					}else if(me.mimavalue == ''){
						uni.showModal({
							title: '提示',
							content: '请输入登录密码！',
							showCancel: false
						});
					}else if(me.mimavalue !== mima){
						uni.showModal({
							title: '提示',
							content: '请输入正确登录密码！',
							showCancel: false
						});
					}else{
						uni.showModal({
							title: '恭喜',
							content: '登录成功！',
							showCancel: false,
							success: function (qwer) {
								if (qwer.confirm) {

									uni.switchTab({
										url: '../index/index',
									});

									





								}
							}
						});
					};
				},
				fail: function(e){
					uni.showModal({
						title: '提示',
						content: '数据库中暂无用户！'
					});
				}
			});
		}
	}
}
